<template>
	<view class="commodity">
		<view v-if="favoriteslist.length == 0" style="margin-top: 50rpx;text-align: center;">
			<text>暂无收藏商品，赶紧去收藏好货吧~</text>
		</view>
		<view v-else>
			<view class="title">
				<text>{{"共"+goodsnum+"件商品"}}</text>
			</view>
			<view class="commoditybox" v-for="(item,index) in favoriteslist" :key="index">
				<view class="box-left">
					<image :src="item.pics|picsfilter"></image>
				</view>
				<view class="box-right" @click="navto(item.id)">
					<view class="name">{{item.model+ ' ' +item.size}}</view>
					<view class="price">{{currencySel(item.currencyId) + ' '+ formatNumberRgx(item.priceIndi)}}</view>
				</view>
				<view class="closeicon" @click="clickclose(item.id)">
					<image src="../../static/imgs/collect/del.png"></image>
				</view>
			</view>
			<view class="bottom" v-if="haveMore == 1">没有更多了</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				goodsnum: 0,
				page: 1,
				favoriteslist: [],
				haveMore: 0
			}
		},
		created() {
			this.favorites()
		},
		filters: {
			picsfilter(str) {
				if (str.indexOf('|') !== -1) {
					return str.substring(0, str.indexOf("|"))
				} else {
					return str
				}
			}
		},
		onPullDownRefresh() {
			this.page = 1;
			this.favoriteslist = [];
			this.haveMore = 0;
			this.favorites();
		},
		onReachBottom() {
			if (this.haveMore == 0) {
				this.page++;
				this.favorites()
			}
		},
		methods: {
			// 获取商品收藏列表
			favorites() {
				uni.request({
					url: this.$baseFileUrl + '/favorites/?page=' + this.page,
					header: {
						"content-type": "application/json",
						token: uni.getStorageSync('token')
					},
					complete: (res) => {
						console.log('商品收藏列表');
						console.log(res.data);
						this.favoriteslist = res.data.list;
						this.goodsnum = res.data.total;
						if (res.data.list.length > 0) {
							this.haveMore = 0;
						} else {
							this.haveMore = 1;
						}
					}
				})
			},
			// 删除
			clickclose(id) {
				uni.request({
					method: "POST",
					url: this.$baseFileUrl + '/change_favorite/?id=' + id,
					header: {
						"content-type": "application/json",
						token: uni.getStorageSync('token')
					},
					success: (res) => {
						console.log(res.data);
						uni.showToast({
							title:"删除成功",
							icon:"none"
						})
						this.favorites();
					},
					fail: (err) => {
						uni.showToast({
							title:err.data.message,
							icon:"none"
						})
					}
				})
			},
			navto(id) {
				uni.navigateTo({
					url: "../index/details?id=" + id
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
	.commodity {
		padding: 0 40rpx 0 40rpx;
		background-color: #fff;

		.title {
			margin-bottom: 26rpx;
			font-size: 20rpx;
		}

		.commoditybox {
			margin-bottom: 40rpx;
			display: flex;
			position: relative;
			box-shadow: -1px 5px 10px 2px #eeeff1;
			border-radius: 16rpx;

			.box-left {
				width: 192rpx;
				height: 174rpx;
				display: flex;
				justify-content: center;
				align-items: center;
				background-color: #fffcf7;

				image {
					width: 140rpx;
					height: 140rpx;
					border-radius: 16rpx;
				}
			}

			.box-right {
				padding: 30rpx 0 40rpx 10rpx;
				display: flex;
				flex-direction: column;
				justify-content: space-between;

				.name {
					font-size: 24rpx;
				}

				.price {
					font-size: 26rpx;
					color: #ff8b62;
				}
			}

			.closeicon {
				width: 40rpx;
				height: 40rpx;
				position: absolute;
				right: -20rpx;
				top: 20rpx;

				image {
					width: 100%;
					height: 100%;
				}
			}
		}

		.bottom {
			font-size: 24rpx;
			color: #e3e3e3;
			text-align: center;
		}
	}
</style>
